import Look from '../../../components/look.vue'
export default {
	components: {
		Look
	},
	data() {
		return {
			title: '经审监督',
			list:[
			]
		}
	},
	created() {
		this.getData()
	},
	methods: {
		getData() {
			var data = {
				category: "财务经审信息发布", 
				channel: "经审监督"
			}
			this.$axios(this.$api.getList , data , 'post')
			.then(res =>{
				console.log(res, '.then--------');
				if(res.error_code == 0){
					this.image
					this.list = res.data.data
				}
			})
			.catch(res =>{
				console.log(res,'.catch--------')
			})
		},
	}
}



